import services from '../services/index';
import getFileStream from '../../../helpers/s3_object_stream/object_readstream';

export default async function postAttendance(req, res, next) {
  try {
    const fileStream = await getFileStream(req.body.key);
    console.log(fileStream);
    const message = await services.parseCSV(fileStream);
    return res.json(message);
  } catch (error) {
    return next(error);
  }
}
